
* {
  padding: 0;
  margin:0;
}
a {
  text-decoration: none;
  cursor: pointer;
}
a:link,a:active,a:visited {
  color: #333;
}
ul {
  list-style: none;
}
img {
  vertical-align: middle;
}
html,body {
  height: 100%;
  font-family: "microsoft YaHei";
}
.layout {
  min-height: 100%;
  position: relative;
  background-color: #f2f2f2;
}
.header {
  background-color: #333;
  height: 60px;
  line-height: 60px;
}
.header ul {
  margin-right: -5px;
}
.header li {
  color: #aaa;
  margin-right: 10px;
  font-size: 13px;
}
.header a {
  font-size: 14px;
  margin-right: 10px;
}
.header a:link,
.header a:active,
.header a:visited {
  color: #aaa;
}
.header-inner {
  width: 1200px;
  margin: 0 auto;
  clear: both;
  overflow: hidden;
  color: #fff;
}
.header-inner img {
  width: 50px;
  height: 50px;
  float: left;
  margin-top: 5px;
}
.header-inner ul {
  float: right;
  list-style: none;
}
.header-inner ul li {
  float: left;
}
.container {
  width: 1200px;
  min-height: 100%;
  margin: 0 auto;    
  padding-bottom: 70px;
  clear: both;
  overflow: hidden;
}
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 70px;
  line-height: 70px;
  background-color: #ddd;
  text-align: center;
}
.about {
  margin-top: 40px;
  text-align: center;
}



